<template>
  <div id ="background">
    <div id ="loginDiv" >
      <label class="label-wrap">系统登录</label>
      <br>
      <el-input class="el-input" v-model="loginForm.username" placeholder="请输入账号"></el-input>
      <br>
      <el-input placeholder="请输入密码"  v-model="loginForm.password" show-password @keyup.enter.native="login"></el-input>
      <el-row>
        <el-button  type="primary" size="medium " v-on:click="login" > 登录 </el-button>
      </el-row>

    </div>
  </div>
</template>

<script>
export default {
  name: 'login',
  created () {
  },
  data () {
    return {
      loginForm: {
        username: '',
        password: ''
      },
      responseResult: []
    }
  },
  methods: {

    login () {
      this.$axios
        .post('/login', {
          username: this.loginForm.username,
          password: this.loginForm.password
        })
        .then(successResponse => {
          if (successResponse.data.code === 200) {
            this.$router.replace({path: '/index'})
          }
        })
        .catch(failResponse => {
        })
    }

  }

}
</script>

<style scoped>
#background {
  height: 100%;
  width: 100%;
  position: fixed;
  top:0px;
  left:0px;
  right:0px;
  bottom:0px;
  /*background: black;*/
  background: url("../../static/image/preview.jpg") no-repeat;
  background-size: cover;
  background-position: center;
}
#loginDiv {
/*  border-radius: 15px;
  background-clip: padding-box;
  margin: 90px auto;
  width: 350px;
  padding: 35px 35px 15px 35px;
  background: #fff;
  border: 1px solid #eaeaea;
  box-shadow: 0 0 25px #cac6c6;*/

  background: whitesmoke;
  position: absolute;
  margin:auto;
  left:0;
  right:0;
  top:0;
  bottom:0;
  height:650px;
  width: 650px;
  text-align: center;
  border-radius:30px;
  box-shadow: 0 0 20px #cac6c6;

  /*
  box-shadow: rgba(0, 0, 0, 0.2) 0px 0px 20px, rgba(0, 0, 0, 0.2) 0px 0px 20px;
  filter:alpha(Opacity=60);-moz-opacity:0.6;opacity: 0.6; //半透明效果
  */

}
.el-input {
  /*float: left; 居左显示*/
  text-align: center;
  width: 400px;
  margin: 20px;
}
.label-wrap {
  Line-height:200px;
  height: 250px;
  width: 600px;
  font-size: 30px;
  text-align: center;
}

</style>



